* {
    box-sizing: border-box;
    margin: 0;
    padding: 0;
  }
  
  body {
    font-family: sans-serif;
  }
  
  .Container {
    width: 100%;
    height: 100vh;
    background-position: center;
    background-size: cover;
    background-repeat: no-repeat;
    display: flex;
    align-items: center;
    justify-content: space-around;
  }
  .game {
    display: inline-block;
    background-color: white;
    border-radius: 50px;
    margin-left: 20%;
  }
  .square {
    background: #fff;
    float: left;
    font-size: 24px;
    font-weight: bold;
    line-height: 34px;
    height: 100px;
    margin-right: -1px;
    margin-top: -1px;
    padding: 0;
    text-align: center;
    width: 100px;
    background: #c9f9fc;
    border: 8px solid #000000;
    border-radius: 16px;
  }
  .square img {
    width: 50%;
  }
  
  .board-row:after {
    clear: both !important;
    content: "" !important;
    display: table !important;
  }
  
  .next {
    display: inline-block;
  }
  .winner_counter {
    display: flex;
  }
  
  .winner_counter .x img,
  .winner_counter .zero img {
    width: 30%;
  }
  .x,
  .zero {
    display: flex;
    justify-content: center;
    align-items: center;
  }
  
  @media only screen and (max-width: 1100px) {
    .Container {
      flex-direction: column !important;
    }
    .game {
      margin-left: 0 !important;
    }
  }
  